import s from "../Kafedra/Kafedra.module.css";
import {useEffect, useState} from "react";
import {_axios} from "../../axios/_axios";
import {Link} from "react-router-dom";

const Kafedra = () => {
  const [data, setData] = useState();

  useEffect(
    () => {
      _axios.get('/people', {credentials: 'include'})
        .then(({data}) => {
          setData(data)
        })
        .catch(e => {
          console.log(e)
        })
    }, [])

  return (
    <div>
      <h1 className={s.main_title}>Наші викладачі</h1>
      <div className={s.main}>
        {data && data.map(person =>
          <div key={person._id}>
            <hr/>
            <div className={s.person}>
              <Link className={s.name} to={'/person/' + person._id}>
                {person.gallery && <img className={s.photo} alt={'person'}
                                        src={person.gallery}/>}
              </Link>
              <div className={s.text}>
                <Link className={s.name} to={'/person/' + person._id}>
                  <h3>{person.fName + ' ' + person.sName}</h3>
                </Link>
                <p><strong> Вчене звання: </strong>{person.scienceTitle}</p>
                <p><strong>Науковий ступінь: </strong>{person.scienceLevel}</p>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default Kafedra;
